  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  height: calc(100 * var(--vh, 1vh));
  overflow: hidden; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.desktop-only {
  display: block; }

.mobile-only {
  display: none; }

@media (max-width: 1023px) {
  .desktop-only {
    display: none; }
  .mobile-only {
    display: block; } }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 35px; }
  @media (max-width: 1023px) {
    .container {
      padding: 0; } }

@keyframes appear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes jump {
  10%, 50% {
    transform: translate(-50%, -10%); }
  30% {
    transform: translate(-50%, -20%); }
  20%, 40%, 60% {
    transform: translate(-50%, 0); } }

@keyframes hit {
  10%, 30%, 50% {
    transform: scale(1); }
  20%, 40% {
    transform: scale(0.95); } }

@keyframes color {
  33% {
    fill: #2c39f2; }
  66% {
    fill: #ff1553; }
  100% {
    fill: #9627a3; } }

@keyframes blink {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes arrrgh {
  10%, 30% {
    transform: rotateZ(-5deg); }
  20%, 40% {
    transform: rotateZ(5deg); }
  50% {
    transform: rotateZ(0); } }

.page-header {
  display: none;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #fff;
  z-index: 10;
  animation: appear 2s linear; }

.page-header--show {
  display: flex; }

.page-header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 25px 0; }

.page-header__toggle {
  display: none; }

@media (max-width: 1023px) {
  .page-header__content {
    height: 74px;
    padding: 25px 24px;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    overflow: hidden;
    transition: height .5s ease-in-out; }
  .page-header__logo {
    margin-bottom: 333px; }
  .page-header__toggle {
    display: flex;
    cursor: pointer;
    background-color: transparent;
    border: none;
    padding: 0;
    margin: 0;
    width: 36px;
    height: 30px;
    position: absolute;
    top: 26px;
    right: 26px;
    overflow: hidden;
    transition: all .5s ease-in-out; }
    .page-header__toggle span {
      position: absolute;
      width: 80%;
      height: 2px;
      left: 3px;
      top: 50%;
      transform: translate(0, -50%);
      background-color: #2c39f2;
      transition: all .5s ease-in-out; }
    .page-header__toggle::before {
      content: "";
      position: absolute;
      top: 6px;
      left: 3px;
      width: 80%;
      height: 2px;
      background-color: #2c39f2;
      transition: all .5s ease-in-out; }
    .page-header__toggle::after {
      content: "";
      position: absolute;
      bottom: 6px;
      left: 3px;
      width: 80%;
      height: 2px;
      background-color: #2c39f2;
      transition: all .5s ease-in-out; }
    .page-header__toggle:hover, .page-header__toggle:focus {
      transform: scale(1.1); }
  .page-header__nav {
    display: none; }
  .page-header--opened .page-header__toggle span {
    left: -50px; }
  .page-header--opened .page-header__toggle::before {
    top: 14px;
    left: 3px;
    transform: rotate(45deg); }
  .page-header--opened .page-header__toggle::after {
    bottom: 14px;
    left: 3px;
    transform: rotate(-45deg); }
  .page-header--opened .page-header__content {
    height: 100vh; }
  .page-header--opened .page-header__nav {
    display: flex; } }

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-column-gap: 24px; }

.main-nav__link {
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  transition: color .5s ease-in-out; }
  .main-nav__link:hover, .main-nav__link:focus {
    color: #2c39f2; }

@media (max-width: 1023px) {
  .main-nav {
    margin: 0 auto; }
  .main-nav__list {
    display: flex;
    flex-direction: column;
    text-align: center; }
  .main-nav__item {
    margin-bottom: 30px; }
  .main-nav__link {
    font-size: 28px;
    line-height: 28px; } }

.welcome-screen {
  width: 100%;
  height: 100vh;
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: top 1s ease-in-out; }

.welcome-screen--hide {
  top: -100vh; }

.welcome-screen__logo {
  animation: appear 2s linear; }
  .welcome-screen__logo svg {
    width: 800px;
    height: 100px; }

#LIGA path,
#FRONT path {
  fill: black; }

.welcome-screen__label {
  font-size: 24px;
  line-height: 22px;
  position: absolute;
  left: 50%;
  bottom: 100px;
  transform: translate(-50%, 0);
  animation: appear 2s linear, jump 2s linear 2s infinite; }

@media (max-width: 1023px) {
  .welcome-screen__logo svg {
    width: 550px;
    height: 70px; } }

.main-screen {
  position: relative;
  width: 100%;
  height: 100vh;
  top: -100vh;
  left: 0;
  display: flex;
  justify-content: center;
  transition: top 1s ease-in-out;
  background-image: url("../../img/A-background.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 644px 651px; }
  .main-screen h1 {
    font-size: 96px;
    line-height: 96px;
    color: #2c39f2;
    font-weight: normal; }

.main-screen--hide {
  top: 0; }

.main-screen__title-container {
  display: flex;
  height: 100%;
  align-items: center;
  margin-right: 40px; }

.main-screen__description-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center; }
  .main-screen__description-container p {
    font-weight: normal;
    font-size: 30px;
    line-height: 40px;
    padding: 0;
    margin: 0; }
  .main-screen__description-container svg {
    margin-bottom: 35px; }

#lightning-violet,
#lightning-blue,
#lightning-red {
  transform-origin: center;
  transform-box: fill-box;
  animation: hit 2s alternate infinite; }
  #lightning-violet path,
  #lightning-blue path,
  #lightning-red path {
    animation: color 2s alternate infinite; }

#scream-argh {
  animation: arrrgh 2s alternate infinite;
  transform-origin: center;
  transform-box: fill-box; }
  #scream-argh path {
    animation: blink 2s alternate infinite; }

@media (max-width: 1023px) {
  .main-screen {
    overflow: hidden;
    background-image: none; }
  .main-screen__title-container {
    position: absolute;
    margin-right: 0;
    width: 100%;
    left: 0;
    padding: 0 24px;
    transition: left 1s ease-in-out; }
  .main-screen__description-container {
    position: absolute;
    width: 100%;
    left: 100%;
    padding: 0 24px;
    background-color: #2c39f2;
    transition: left 1s ease-in-out; }
    .main-screen__description-container p {
      color: white; }
  #lightning-violet,
  #lightning-blue,
  #lightning-red {
    transform-origin: center;
    transform-box: fill-box;
    animation: hit 2s alternate infinite; }
    #lightning-violet path,
    #lightning-blue path,
    #lightning-red path {
      fill: white;
      animation: none; }
  #flag path {
    fill: white;
    stroke: black; }
  #scream-argh path {
    fill: white; }
  .main-screen--toggle .main-screen__title-container {
    left: -100%; }
  .main-screen--toggle .main-screen__description-container {
    left: 0; } }
